@import url("https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:ital,wght@0,100;0,400;0,500;1,100;1,500&display=swap");
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-bottom: 0;
  font-size: 16px;
  letter-spacing: 1px;
  color: #727171;
  overflow-x: hidden;
  font-family: "Barlow Semi Condensed", sans-serif;
}

* {
  margin: 0;
  /* overflow-x: hidden; */
  padding: 0;
}
.locate-container {
  position: relative;
  margin: 0 auto;
  width: 90%;
}
.details h3 {
  line-height: 20px;
  font-size: 30px;
  padding: 20px 0px;
  border-top: 3px solid rgba(0, 0, 0, 0.5);
  border-bottom: 3px solid rgba(0, 0, 0, 0.5);
}
footer {
  background-color: rgb(0 0 0 / 8%);
  margin-top: 30px;
}
.footer-container {
  padding: 2rem;
  margin: 0 auto;
  width: 80%;
}
.footer-content {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

footer ul {
  display: flex;
  margin: 10px;
  flex-direction: column;
}
footer ul li {
  margin: 10px;
}
.locate-container h4 {
  margin-bottom: 100px;
}
.nav-body {
  display: flex;
  justify-content: space-between;
}
.nav-body ul {
  flex-direction: column;
  margin: 50px;
}
.nav-body ul li {
  margin: 20px;
}
.nav-body,
.nav-body > * {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.nav-body ul {
  display: flex;
  flex-direction: column;
  font-size: 25px;
}
.none {
  display: none;
}
.responsive-nav {
  position: relative;
}

#close-btn {
  position: absolute;
  padding: 20px;
  z-index: 500;
  background-color: red;
  right: 10px;
  top: 50px;
  color: #fff;
}
.fixed {
  background: #fff;
  position: fixed;
  width: 100%;
  box-sizing: border-box;
  box-shadow: 3px 3px 3px rgb(0 0 0 / 10%);
  color: #fff;
}
@media (max-width: 1280px) {
  .nav-body {
    display: initial;
    background-image: url("./img/responsive.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    position: relative;
    background-position: center;
  }
  .nav-body img {
    display: none;
  }
  .nav-body ul {
    text-align: center;
    justify-content: center;
  }
  .nav-body ul {
    width: 70%;
    color: #fff;
    margin: 0 auto;
  }
  .nav-body ul,
  .nav-body ul li {
  }
  .nav-body li {
    padding: 20px;
    font-size: 30px;
    background: #29a4d5;
    font-size: 30px;
    box-shadow: 4px 3px 20px;
  }
}
