.am-flex img {
  display: flex;
  width: 50px;
}
.am-flex {
  display: flex;
  align-items: center;
}
.am-container {
  margin-top: 50px;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}
.am {
  position: relative;
  margin: 40px auto;
  width: 90%;
}
.am-desc {
  margin-left: 10px;
}
@media (max-width: 900px) {
  .am-container {
    max-width: 960px;
    grid-template-columns: repeat(2, 1fr);
  }
  .am {
    margin: 0 auto;
    width: 90%;
  }
}

@media (max-width: 700px) {
  .am-container {
    max-width: 960px;
    margin: 0 auto;
    grid-template-columns: repeat(1, 1fr);
  }
  .am {
    margin: 0 auto;
    width: 90%;
  }
}

h4 {
  margin-bottom: 20px;
}

button {
  padding: 10px 15px;
  margin: 20px;
  border: none;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  color: #fff;
  border-radius: 20px;
}
.primary {
  background-color: rgb(231, 62, 62);
}
.secondary {
  background-color: rgb(85, 247, 107);
}
button:hover {
  box-shadow: 4px 4px 20px 5px rgba(0, 0, 0, 0.5);
  transform: scale(1.12);
  transition: all 0.4s;
}
.absolutely-positioned-text {
  -webkit-user-select: none;
  user-select: none;
  margin: 0px;
  font-size: 8.063em;
  font-family: inherit;
  color: rgba(2, 0, 0, 0.2);
  position: absolute;
  right: 1px;
  top: -20px;
  transform: translateY(-20px);
  transition: all 0.4s ease-out;
  -webkit-transition: all 0.4s ease-out;
}
.am h4 {
  margin-bottom: 140px;
}

@media (max-width: 1300px) {
  .absolutely-positioned-text {
    display: none;
  }
}
