.flex-container {
  max-width: 1000px;
  position: relative;
  margin: 0 auto;
}
.flex {
  display: flex;
  margin-top: 30px;
  align-items: center;
}
.flex-container .absolutely-positioned-text {
  right: -190px;
}
img {
  width: 100%;
}
.image-container {
  width: 50%;
  margin-right: 50px;
}
.text-div {
  width: 50%;
  margin-left: 50px;
}

h2 {
  margin-bottom: 20px;
}

@media (max-width: 700px) {
  .flex {
    flex-direction: column;
    margin: 0 auto;
    width: 80%;
  }
  .text-div,
  .image-container {
    margin: 0;
    width: 100%;
  }
}
