.hero {
  background-image: url("./../img/hero-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 550px;
  position: relative;
  background-position: center;
}
a {
  text-decoration: none;
}
nav {
  display: flex;
  padding: 20px;
  z-index: 500;
  justify-content: space-between;
}
.hero ul {
  display: flex;
  margin: 0;
  color: rgba(0, 0, 0, 0.818);
  list-style: none;
}

li {
  display: inline;
  margin: 0px 10px;
}
.content-heading {
  position: absolute;
  color: #fff;
  bottom: 30px;
  margin: auto;
  width: 100%;
}

.content-heading h1,
.content-heading h3 {
  text-align: center;
}
.nav-container img {
  width: 100%;
}
.nav-container {
  display: flex;
}
