@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:ital,wght@0,100;0,400;0,500;1,100;1,500&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-bottom: 0;
  font-size: 16px;
  letter-spacing: 1px;
  color: #727171;
  overflow-x: hidden;
  font-family: "Barlow Semi Condensed", sans-serif;
}

* {
  margin: 0;
  /* overflow-x: hidden; */
  padding: 0;
}
.locate-container {
  position: relative;
  margin: 0 auto;
  width: 90%;
}
.details h3 {
  line-height: 20px;
  font-size: 30px;
  padding: 20px 0px;
  border-top: 3px solid rgba(0, 0, 0, 0.5);
  border-bottom: 3px solid rgba(0, 0, 0, 0.5);
}
footer {
  background-color: rgb(0 0 0 / 8%);
  margin-top: 30px;
}
.footer-container {
  padding: 2rem;
  margin: 0 auto;
  width: 80%;
}
.footer-content {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

footer ul {
  display: flex;
  margin: 10px;
  flex-direction: column;
}
footer ul li {
  margin: 10px;
}
.locate-container h4 {
  margin-bottom: 100px;
}
.nav-body {
  display: flex;
  justify-content: space-between;
}
.nav-body ul {
  flex-direction: column;
  margin: 50px;
}
.nav-body ul li {
  margin: 20px;
}
.nav-body,
.nav-body > * {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.nav-body ul {
  display: flex;
  flex-direction: column;
  font-size: 25px;
}
.none {
  display: none;
}
.responsive-nav {
  position: relative;
}

#close-btn {
  position: absolute;
  padding: 20px;
  z-index: 500;
  background-color: red;
  right: 10px;
  top: 50px;
  color: #fff;
}
.fixed {
  background: #fff;
  position: fixed;
  width: 100%;
  box-sizing: border-box;
  box-shadow: 3px 3px 3px rgb(0 0 0 / 10%);
  color: #fff;
}
@media (max-width: 1280px) {
  .nav-body {
    display: inline;
    display: initial;
    background-image: url(/static/media/responsive.ea0bf164.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    position: relative;
    background-position: center;
  }
  .nav-body img {
    display: none;
  }
  .nav-body ul {
    text-align: center;
    justify-content: center;
  }
  .nav-body ul {
    width: 70%;
    color: #fff;
    margin: 0 auto;
  }
  .nav-body ul,
  .nav-body ul li {
  }
  .nav-body li {
    padding: 20px;
    font-size: 30px;
    background: #29a4d5;
    font-size: 30px;
    box-shadow: 4px 3px 20px;
  }
}

.am-flex img {
  display: flex;
  width: 50px;
}
.am-flex {
  display: flex;
  align-items: center;
}
.am-container {
  margin-top: 50px;
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
}
.am {
  position: relative;
  margin: 40px auto;
  width: 90%;
}
.am-desc {
  margin-left: 10px;
}
@media (max-width: 900px) {
  .am-container {
    max-width: 960px;
    grid-template-columns: repeat(2, 1fr);
  }
  .am {
    margin: 0 auto;
    width: 90%;
  }
}

@media (max-width: 700px) {
  .am-container {
    max-width: 960px;
    margin: 0 auto;
    grid-template-columns: repeat(1, 1fr);
  }
  .am {
    margin: 0 auto;
    width: 90%;
  }
}

h4 {
  margin-bottom: 20px;
}

button {
  padding: 10px 15px;
  margin: 20px;
  border: none;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  color: #fff;
  border-radius: 20px;
}
.primary {
  background-color: rgb(231, 62, 62);
}
.secondary {
  background-color: rgb(85, 247, 107);
}
button:hover {
  box-shadow: 4px 4px 20px 5px rgba(0, 0, 0, 0.5);
  transform: scale(1.12);
  transition: all 0.4s;
}
.absolutely-positioned-text {
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
  margin: 0px;
  font-size: 8.063em;
  font-family: inherit;
  color: rgba(2, 0, 0, 0.2);
  position: absolute;
  right: 1px;
  top: -20px;
  transform: translateY(-20px);
  transition: all 0.4s ease-out;
  -webkit-transition: all 0.4s ease-out;
}
.am h4 {
  margin-bottom: 140px;
}

@media (max-width: 1300px) {
  .absolutely-positioned-text {
    display: none;
  }
}

.flex-container {
  max-width: 1000px;
  position: relative;
  margin: 0 auto;
}
.flex {
  display: flex;
  margin-top: 30px;
  align-items: center;
}
.flex-container .absolutely-positioned-text {
  right: -190px;
}
img {
  width: 100%;
}
.image-container {
  width: 50%;
  margin-right: 50px;
}
.text-div {
  width: 50%;
  margin-left: 50px;
}

h2 {
  margin-bottom: 20px;
}

@media (max-width: 700px) {
  .flex {
    flex-direction: column;
    margin: 0 auto;
    width: 80%;
  }
  .text-div,
  .image-container {
    margin: 0;
    width: 100%;
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(4, 0.8fr);
  justify-content: center;
  grid-gap: 10px;
}
.grid-container {
  width: 90%;
  margin: 20px auto;
}

.hero {
  background-image: url(/static/media/hero-bg.ae219505.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 550px;
  position: relative;
  background-position: center;
}
a {
  text-decoration: none;
}
nav {
  display: flex;
  padding: 20px;
  z-index: 500;
  justify-content: space-between;
}
.hero ul {
  display: flex;
  margin: 0;
  color: rgba(0, 0, 0, 0.818);
  list-style: none;
}

li {
  display: inline;
  margin: 0px 10px;
}
.content-heading {
  position: absolute;
  color: #fff;
  bottom: 30px;
  margin: auto;
  width: 100%;
}

.content-heading h1,
.content-heading h3 {
  text-align: center;
}
.nav-container img {
  width: 100%;
}
.nav-container {
  display: flex;
}

.project-details {
  padding-top: 20px;
  margin: 0 auto;
  width: 85%;
  display: flex;
}
.project-detail {
  display: flex;
  align-items: center;
  margin: 0px 10px;
}
.icon-prefix {
  margin: 10px;
}
.project-details-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.resources {
  margin: 0 auto;
  position: relative;
  width: 90%;
}
.flex {
  display: flex;
  justify-content: space-around;
}

h5 {
  font-size: large;
}
.content-flex {
  display: flex;
  align-items: center;
}
.content-flex img {
  width: 50px;
  margin: 0 10px;
}
.resources h4 {
  margin-bottom: 100px;
}

